<template>
	<div id="play">
		<div class="search-main">
			<form class="p-search-input">
				<label class="search-label">
					<i class="iconfont icon-sousuo"></i>
					<input type="" placeholder="/"/>
				</label>				
			</form>
			<div class="select-box">
				<!--<button @click="extend()" class="select-item" v-bind:class="{'base-color':changeColor}"><i class="iconfont icon-down-trangle1"></i></button>
				<button @click="extend()" class="select-item"><i class="iconfont icon-down-trangle1"></i></button>
				<button @click="extend()" class="select-item"><i class="iconfont icon-down-trangle1"></i></button>-->
				<button id="button" v-for="(v,i) in tabs" @click="extend(i)" class="select-item" v-bind:class="{'base-color':disp==i}">{{v}}<i class="iconfont icon-down-trangle1"></i></button>
			</div>
			<ul class="classify" v-show="togglec==0" ref="btn">
				<li v-for="(v,i) in selects">{{v.itemName}}</li>
			</ul>
			<ul class="classify" v-show="togglec==2" ref="btn">
				<li v-for="(v,i) in sort">{{v.sortItem}}</li>
			</ul>
			<div class="classify" v-show="togglec==1" ref="btn"></div>
			<section>
				<ul class="search-result-product">
					<li v-for="item in products">
						<a href="">
							<img :src="item.imgSrc"/>
							<h3>{{item.proTitle}}</h3>
							<h4>{{item.proDesc}}</h4>
							<span>{{item.price}}</span>
						</a>
					</li>
				</ul>
			</section>
			
		</div>
	</div>
</template>

<script>
	import '../../assets/css/play.css'
	export default{
		data(){
			return{
				tabs:['','',''],
				selects:[
					{itemName:''},{itemName:''},{itemName:''},{itemName:''}
					,{itemName:''},{itemName:''},{itemName:''},{itemName:''}
					,{itemName:''}
				],
				sort:[
					{sortItem:''},{sortItem:''},{sortItem:''},{sortItem:''}
				],
				products:[
				    {imgSrc:require('../../assets/img/product1.jpg'),proTitle:'',proDesc:'PADI+76',price:'9888'},
				    {imgSrc:require('../../assets/img/product2.jpg'),proTitle:'',proDesc:'Feekrclub | 1514',price:'119900'},
				    {imgSrc:require('../../assets/img/product3.jpg'),proTitle:'',proDesc:'76',price:'3980'},
				    {imgSrc:require('../../assets/img/product4.jpg'),proTitle:'',proDesc:'+',price:'1100'},
				    {imgSrc:require('../../assets/img/product5.jpg'),proTitle:'',proDesc:'|++',price:'1250'},
				    {imgSrc:require('../../assets/img/product6.jpg'),proTitle:'',proDesc:'76',price:'5330'},
				    {imgSrc:require('../../assets/img/product7.jpg'),proTitle:'',proDesc:'Feekr|76',price:'2580'},
				    {imgSrc:require('../../assets/img/product8.jpg'),proTitle:'',proDesc:'|76',price:'3880'},
				    {imgSrc:require('../../assets/img/product9.jpg'),proTitle:'',proDesc:'65PADI',price:'3660'},
				],
				togglec:"",
				disp:""
			}
			
		},
		methods:{
			extend(str){
				console.log(str)
				this.togglec=str;
//				this.disp=!this.disp
				this.disp=str;

				console.log(this.$refs.btn.style.display)
				var par = this.$refs.btn.style.display
				if(par == ''){
					this.$refs.btn.style.display='none'
				}else{
					this.$refs.btn.style.display=''
				}				
			}
		}
	}
</script>

<style>
</style>


